@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-nueva-tarea {
   display: flex;
   justify-content: center;

   .agregar-tarea {
      @include m.boton(v.$cyan, v.$blanco);
   }
}

.listado-tareas {
   list-style: none;
   padding: 0;

   .no-tareas {
      text-align: center;
      margin-top: 3rem;
   }
}

.tarea {
   padding: 0.5rem 2rem;
   border-bottom: 2px solid darken(v.$grisClaro, 5%);
   margin-bottom: 1rem;
   display: flex;
   justify-content: space-between;
   align-items: center;
   &:last-of-type {
      border-bottom: none;
   }

   p {
      font-size: 1.4rem;
   }
}

.opciones {
   display: flex;
   gap: 1rem;

   button {
      border: none;
      padding: 0.5rem 1rem;
      text-transform: uppercase;
      font-weight: 700;
      border-radius: 1rem;
      font-size: 1rem;
      &:hover {
         cursor: pointer;
      }
   }
   .completada {
      background-color: v.$azul;
      color: v.$blanco;
   }
   .pendiente {
      background-color: v.$naranja;
      color: v.$blanco;
   }
   .eliminar-tarea {
      background-color: v.$rojo;
      color: v.$blanco;
   }
}
