@use "base/variables" as v;
@use "base/mixins" as m;

.sidebar {
   background-color: v.$indigo;

   opacity: 0;
   height: 0;
   overflow: hidden;
   transition-property: opacity;
   transition-duration: 500ms;

   &.mostrar {
      opacity: 1;
      padding: 2rem;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgb(0 0 0 / 0.9);

      @include m.tablet {
         display: none;
      }
   }

   &.ocultar {
      opacity: 0;
   }

   @include m.tablet {
      height: 100%;
      opacity: 1;
      background-color: v.$indigo;
      padding: 2rem;
   }

   .contenedor-sidebar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      h2 {
         color: v.$blanco;
         margin: 0;
         text-align: center;
      }
      .cerrar-menu {
         display: flex;
         justify-content: center;
         height: 2rem;
         width: 2rem;

         img {
            &:hover {
               cursor: pointer;
            }
            @include m.tablet {
               opacity: 0;
            }
         }
      }
   }

   .sidebar-nav {
      margin-top: 5rem;

      a {
         color: v.$blanco;
         display: block;
         margin-bottom: 2rem;
         padding: 0.7rem;
         transition-property: background-color;
         transition-duration: 300ms;
         text-transform: uppercase;
         font-weight: bold;
         font-size: 2rem;
         text-align: center;

         &:hover,
         &.activo {
            background-color: darken(v.$indigo, 20%);
            border-radius: 0.5rem;
         }

         @include m.tablet {
            text-align: left;
            font-size: 1.3rem;
            margin-bottom: 1.5rem;
         }
      }
   }

   .mostrar {
      display: block;
   }

   .cerrar-sesion-mobile {
      margin-top: 10rem;
      .cerrar-sesion {
         @include m.boton(v.$indigo, v.$blanco);
      }
      @include m.tablet {
         display: none;
      }
   }
}
