@use "base/mixins" as m;
@use "base/variables" as v;

@media (prefers-color-scheme: dark) {
   $grisOscuro: #2c2c3b;

   body {
      background-color: $grisOscuro;
   }

   .tagline,
   .formulario label {
      color: v.$blanco;
   }

   input::placeholder {
      color: darken(v.$grisOscuro, 10%);
   }

   .descripcion-pagina,
   .nombre-pagina {
      color: v.$grisClaro;
   }

   .formulario .campo input {
      background-color: lighten(v.$grisOscuro, 15%);
   }

   .filtros {
      background-color: lighten($grisOscuro, 15%);

      h2,
      label {
         color: v.$blanco;
      }

      input[type="radio"] {
         accent-color: v.$naranja;
      }
   }

   .sidebar {
      @include m.tablet {
         background-color: darken($grisOscuro, 5%);

         .sidebar-nav a {
            &.activo,
            &:hover {
               background-color: v.$naranja;
            }
         }
      }
   }

   .barra-mobile,
   .barra {
      background-color: darken($grisOscuro, 5%);
   }

   .barra {
      p {
         color: v.$naranja;
         span {
            color: v.$grisClaro;
         }
      }
      .cerrar-sesion {
         @include m.boton(v.$naranja, v.$blanco);
         padding: 1rem;
         font-size: 1.2rem;
      }
   }

   .dashboard {
      background-color: $grisOscuro;

      .enlace {
         color: darken(v.$grisClaro, 15%);

         &:hover {
            color: v.$grisClaro;
         }
      }
   }

   .listado-proyectos .proyecto {
      background-color: lighten($grisOscuro, 20%);

      &:hover {
         background-color: lighten($grisOscuro, 10%);
      }
   }

   .listado-tareas .tarea {
      background-color: lighten($grisOscuro, 20%);
      border: none;
      p {
         color: v.$grisClaro;
      }
   }

   .modal .formulario {
      background-color: $grisOscuro;
      input[type="text"] {
         border: none;
         color: v.$blanco;
      }
      legend {
         color: v.$grisClaro;
      }
   }
}
